'use client';
import { useState, useEffect } from 'react';
import { useTelegram } from '@/components/providers/TelegramProvider';
import { Users, Check, X, Crown, AlertCircle, Lock, Globe, Link } from 'lucide-react';

interface RegistrationScreenProps {
  onComplete: () => void;
}

const COUNTRIES = [
  { code: 'US', name: 'United States', flag: 'üá∫üá∏' },
  { code: 'GB', name: 'United Kingdom', flag: 'üá¨üáß' },
  { code: 'RU', name: 'Russia', flag: 'üá∑üá∫' },
  { code: 'UA', name: 'Ukraine', flag: 'üá∫üá¶' },
  { code: 'DE', name: 'Germany', flag: 'üá©üá™' },
  { code: 'FR', name: 'France', flag: 'üá´üá∑' },
  { code: 'ES', name: 'Spain', flag: 'üá™üá∏' },
  { code: 'IT', name: 'Italy', flag: 'üáÆüáπ' },
  { code: 'PL', name: 'Poland', flag: 'üáµüá±' },
  { code: 'TR', name: 'Turkey', flag: 'üáπüá∑' },
  { code: 'BR', name: 'Brazil', flag: 'üáßüá∑' },
  { code: 'IN', name: 'India', flag: 'üáÆüá≥' },
  { code: 'CN', name: 'China', flag: 'üá®üá≥' },
  { code: 'JP', name: 'Japan', flag: 'üáØüáµ' },
  { code: 'KR', name: 'South Korea', flag: 'üá∞üá∑' },
  { code: 'OTHER', name: 'Other', flag: 'üåç' },
];

const getCountryFromLanguage = (langCode?: string): string => {
  if (!langCode) return '';
  const langToCountry: Record<string, string> = {
    'en': 'US', 'ru': 'RU', 'uk': 'UA', 'de': 'DE', 'fr': 'FR',
    'es': 'ES', 'it': 'IT', 'pl': 'PL', 'tr': 'TR', 'pt': 'BR',
    'hi': 'IN', 'zh': 'CN', 'ja': 'JP', 'ko': 'KR',
  };
  return langToCountry[langCode] || '';
};

export function RegistrationScreen({ onComplete }: RegistrationScreenProps) {
  const { user, isPremium, referralCode: urlReferralCode } = useTelegram();
  const [nickname, setNickname] = useState('');
  const [country, setCountry] = useState('');
  const [referralCode, setReferralCode] = useState('');
  const [referrerInfo, setReferrerInfo] = useState<{ nickname: string; username: string } | null>(null);
  const [referralValidating, setReferralValidating] = useState(false);
  const [referralError, setReferralError] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    if (user?.username && !nickname) {
      setNickname(user.username);
    }
    if (user?.language_code && !country) {
      const detectedCountry = getCountryFromLanguage(user.language_code);
      setCountry(detectedCountry);
    }
  }, [user]);

  useEffect(() => {
    if (urlReferralCode && !referralCode) {
      setReferralCode(urlReferralCode);
      validateReferralCode(urlReferralCode);
    }
  }, [urlReferralCode]);

  const validateReferralCode = async (code: string) => {
    if (!code || code.length < 3) {
      setReferrerInfo(null);
      setReferralError('');
      return;
    }

    setReferralValidating(true);
    setReferralError('');

    try {
      const response = await fetch(`/api/referral/validate?code=${code}`);
      const data = await response.json();

      if (data.valid) {
        setReferrerInfo({
          nickname: data.referrer.nickname,
          username: data.referrer.username
        });
        setReferralError('');
      } else {
        setReferrerInfo(null);
        setReferralError('Invalid referral code');
      }
    } catch (err) {
      setReferrerInfo(null);
      setReferralError('Could not validate code');
    } finally {
      setReferralValidating(false);
    }
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      if (referralCode && !urlReferralCode) {
        validateReferralCode(referralCode);
      }
    }, 500);
    return () => clearTimeout(timer);
  }, [referralCode]);

  const checks = {
    premium: isPremium,
    channel: true,
    age: true
  };

  const isValidNickname = (text: string) => /^[a-zA-Z0-9_]+$/.test(text);
  const hasInvalidChars = nickname.length > 0 && !isValidNickname(nickname);
  const canProceed = nickname.length >= 3 && isValidNickname(nickname);

  const handleNicknameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setNickname(e.target.value.slice(0, 20));
  };

  const handleRegister = async () => {
    if (!canProceed) return;
    setLoading(true);
    setError('');

    try {
      const response = await fetch('/api/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          telegramId: user?.id,
          telegramUsername: user?.username,
          nickname: nickname,
          isPremium: isPremium,
          country: country || null,
          referralCode: referralCode || null
        })
      });

      if (response.ok) {
        setTimeout(() => onComplete(), 500);
      } else {
        setError('Registration failed. Please try again.');
        setLoading(false);
      }
    } catch (err) {
      setTimeout(() => onComplete(), 1000);
    }
  };

  const containerStyle: React.CSSProperties = {
    width: '28rem',
    maxWidth: '28rem',
    minWidth: '28rem',
    boxSizing: 'border-box'
  };

  return (
    <div style={{ 
      minHeight: '100vh',
      background: 'linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%)',
      position: 'relative',
      overflow: 'hidden'
    }}>
      <div style={{
        position: 'absolute',
        top: '10%',
        left: '10%',
        width: '300px',
        height: '300px',
        background: 'radial-gradient(circle, rgba(139, 92, 246, 0.3) 0%, transparent 70%)',
        borderRadius: '50%',
        animation: 'pulse 4s ease-in-out infinite'
      }}></div>

      <div style={{
        position: 'absolute',
        bottom: '15%',
        right: '15%',
        width: '250px',
        height: '250px',
        background: 'radial-gradient(circle, rgba(236, 72, 153, 0.3) 0%, transparent 70%)',
        borderRadius: '50%',
        animation: 'pulse 5s ease-in-out infinite'
      }}></div>

      <div style={{
        position: 'relative',
        zIndex: 10,
        padding: '2rem',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '100vh'
      }}>
        <div style={{
          ...containerStyle,
          textAlign: 'center',
          marginBottom: '2rem'
        }}>
          <div style={{
            fontSize: '3rem',
            marginBottom: '1rem'
          }}>üíé</div>
          <h1 style={{
            fontSize: '2.5rem',
            fontWeight: 800,
            background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            marginBottom: '0.5rem'
          }}>
            Matrix TON
          </h1>
          <p style={{
            fontSize: '1rem',
            color: '#9ca3af'
          }}>
            Registration
          </p>
        </div>

        <div style={{
          ...containerStyle,
          backgroundColor: 'rgba(255, 255, 255, 0.05)',
          backdropFilter: 'blur(10px)',
          borderRadius: '1rem',
          padding: '1.5rem',
          border: '1px solid rgba(255, 255, 255, 0.1)',
          marginBottom: '1.5rem'
        }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', marginBottom: '1rem' }}>
            <div style={{
              width: '3.5rem',
              height: '3.5rem',
              borderRadius: '50%',
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontSize: '1.5rem',
              flexShrink: 0
            }}>
              {user?.first_name?.charAt(0) || '?'}
            </div>
            <div style={{ flex: 1, minWidth: 0 }}>
              <p style={{
                fontSize: '1.125rem',
                fontWeight: 600,
                color: '#ffffff',
                marginBottom: '0.25rem',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap'
              }}>
                {user?.first_name} {user?.last_name}
              </p>
              <p style={{
                fontSize: '0.875rem',
                color: '#9ca3af',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap'
              }}>
                @{user?.username || 'unknown'}
              </p>
            </div>
          </div>

          <div style={{ display: 'flex', flexDirection: 'column', gap: '0.75rem' }}>
            {[
              { key: 'premium', label: 'Telegram Premium', icon: Crown, status: checks.premium },
              { key: 'channel', label: 'Channel Subscription', icon: Users, status: checks.channel },
              { key: 'age', label: 'Account Age (12+ months)', icon: AlertCircle, status: checks.age }
            ].map(({ key, label, icon: Icon, status }) => (
              <div key={key} style={{
                display: 'flex',
                alignItems: 'center',
                gap: '0.75rem',
                padding: '0.75rem',
                borderRadius: '0.5rem',
                backgroundColor: status ? 'rgba(34, 197, 94, 0.1)' : 'rgba(239, 68, 68, 0.1)',
                border: `1px solid ${status ? 'rgba(34, 197, 94, 0.3)' : 'rgba(239, 68, 68, 0.3)'}`
              }}>
                <Icon style={{
                  width: '1.25rem',
                  height: '1.25rem',
                  color: status ? '#4ade80' : '#f87171',
                  flexShrink: 0
                }} />
                <span style={{
                  flex: 1,
                  fontSize: '0.875rem',
                  fontWeight: 500,
                  color: '#e5e7eb'
                }}>
                  {label}
                </span>
                {status ? (
                  <Check style={{ width: '1.25rem', height: '1.25rem', color: '#4ade80', flexShrink: 0 }} />
                ) : (
                  <X style={{ width: '1.25rem', height: '1.25rem', color: '#f87171', flexShrink: 0 }} />
                )}
              </div>
            ))}
          </div>
        </div>

        <div style={{ ...containerStyle, marginBottom: '1.5rem' }}>
          <label style={{
            display: 'block',
            fontSize: '0.875rem',
            fontWeight: 600,
            marginBottom: '0.75rem',
            color: '#e5e7eb'
          }}>
            Choose your nickname
          </label>
          <input
            type="text"
            value={nickname}
            onChange={handleNicknameChange}
            placeholder="CryptoKing_2024"
            style={{
              width: '100%',
              padding: '1rem 1.25rem',
              borderRadius: '0.75rem',
              fontSize: '1rem',
              fontWeight: 500,
              color: '#ffffff',
              backgroundColor: 'rgba(255, 255, 255, 0.1)',
              border: `2px solid ${hasInvalidChars ? 'rgba(239, 68, 68, 0.6)' : 'rgba(156, 163, 175, 0.3)'}`,
              outline: 'none',
              transition: 'all 0.3s',
              boxSizing: 'border-box'
            }}
          />
          <div style={{
            fontSize: '0.75rem',
            marginTop: '0.5rem',
            fontWeight: 500,
            color: hasInvalidChars ? '#f87171' : nickname.length >= 3 ? '#4ade80' : '#9ca3af'
          }}>
            {hasInvalidChars 
              ? '‚ùå Only Latin letters (a-z, A-Z), numbers (0-9), and underscore (_)' 
              : `${nickname.length}/20${nickname.length >= 3 ? ' ‚Ä¢ ‚úì Valid' : ' ‚Ä¢ Min 3 chars'}`
            }
          </div>

          <div style={{
            display: 'flex',
            alignItems: 'flex-start',
            gap: '0.5rem',
            marginTop: '0.75rem',
            padding: '0.75rem',
            borderRadius: '0.5rem',
            background: 'rgba(251, 191, 36, 0.1)',
            border: '1px solid rgba(251, 191, 36, 0.3)'
          }}>
            <Lock style={{
              width: '1rem',
              height: '1rem',
              color: '#fbbf24',
              flexShrink: 0,
              marginTop: '0.125rem'
            }} />
            <div style={{ flex: 1 }}>
              <p style={{
                fontSize: '0.7rem',
                color: '#fcd34d',
                lineHeight: '1.4'
              }}>
                Cannot be changed! Linked to your @{user?.username || 'username'} forever.
              </p>
            </div>
          </div>

          <div style={{
            display: 'flex',
            alignItems: 'flex-start',
            gap: '0.5rem',
            marginTop: '0.5rem',
            padding: '0.75rem',
            borderRadius: '0.5rem',
            background: 'rgba(34, 197, 94, 0.1)',
            border: '1px solid rgba(34, 197, 94, 0.3)'
          }}>
            <AlertCircle style={{
              width: '1rem',
              height: '1rem',
              color: '#4ade80',
              flexShrink: 0,
              marginTop: '0.125rem'
            }} />
            <div style={{ flex: 1 }}>
              <p style={{
                fontSize: '0.7rem',
                color: '#86efac',
                lineHeight: '1.4'
              }}>
                Privacy Protected - @{user?.username || 'username'} stays private, only nickname is visible.
              </p>
            </div>
          </div>
        </div>

        <div style={{ ...containerStyle, marginBottom: '1.5rem' }}>
          <label style={{
            display: 'block',
            fontSize: '0.875rem',
            fontWeight: 600,
            marginBottom: '0.75rem',
            color: '#e5e7eb'
          }}>
            Referral Code (Optional)
          </label>
          <input
            type="text"
            value={referralCode}
            onChange={(e) => setReferralCode(e.target.value.toUpperCase().slice(0, 10))}
            placeholder="Enter referral code"
            disabled={!!urlReferralCode}
            style={{
              width: '100%',
              padding: '1rem 1.25rem',
              borderRadius: '0.75rem',
              fontSize: '1rem',
              fontWeight: 500,
              color: '#ffffff',
              backgroundColor: urlReferralCode ? 'rgba(255, 255, 255, 0.05)' : 'rgba(255, 255, 255, 0.1)',
              border: `2px solid ${
                referralError ? 'rgba(239, 68, 68, 0.6)' : 
                referrerInfo ? 'rgba(34, 197, 94, 0.6)' : 
                'rgba(156, 163, 175, 0.3)'
              }`,
              outline: 'none',
              transition: 'all 0.3s',
              boxSizing: 'border-box',
              cursor: urlReferralCode ? 'not-allowed' : 'text'
            }}
          />
          <div style={{
            fontSize: '0.75rem',
            marginTop: '0.5rem',
            fontWeight: 500,
            color: referralError ? '#f87171' : referrerInfo ? '#4ade80' : '#9ca3af'
          }}>
            {referralValidating ? '‚è≥ Validating...' : 
             referralError ? `‚ùå ${referralError}` :
             referrerInfo ? `‚úì Invited by @${referrerInfo.username}` :
             referralCode ? 'Enter valid code or leave empty' :
             'Optional - leave empty to join under MASTER'}
          </div>

          {referrerInfo && (
            <div style={{
              display: 'flex',
              alignItems: 'center',
              gap: '0.5rem',
              marginTop: '0.75rem',
              padding: '0.75rem',
              borderRadius: '0.5rem',
              background: 'rgba(34, 197, 94, 0.1)',
              border: '1px solid rgba(34, 197, 94, 0.3)'
            }}>
              <Link style={{ 
                width: '1rem', 
                height: '1rem', 
                color: '#4ade80',
                flexShrink: 0
              }} />
              <div style={{ flex: 1 }}>
                <p style={{
                  fontSize: '0.75rem',
                  fontWeight: 600,
                  color: '#4ade80',
                  marginBottom: '0.25rem'
                }}>
                  Your Referrer
                </p>
                <p style={{
                  fontSize: '0.7rem',
                  color: '#86efac',
                  lineHeight: '1.4'
                }}>
                  {referrerInfo.nickname} (@{referrerInfo.username})
                </p>
              </div>
            </div>
          )}

          {!referralCode && !referralValidating && (
            <div style={{
              display: 'flex',
              alignItems: 'flex-start',
              gap: '0.5rem',
              marginTop: '0.75rem',
              padding: '0.75rem',
              borderRadius: '0.5rem',
              background: 'rgba(251, 191, 36, 0.1)',
              border: '1px solid rgba(251, 191, 36, 0.3)'
            }}>
              <AlertCircle style={{ 
                width: '1rem', 
                height: '1rem', 
                color: '#fbbf24',
                flexShrink: 0,
                marginTop: '0.125rem'
              }} />
              <div style={{ flex: 1 }}>
                <p style={{
                  fontSize: '0.7rem',
                  color: '#fcd34d',
                  lineHeight: '1.4'
                }}>
                  No referral code. You will be placed under MASTER.
                </p>
              </div>
            </div>
          )}
        </div>

        <div style={{ ...containerStyle, marginBottom: '1.5rem' }}>
          <label style={{
            display: 'block',
            fontSize: '0.875rem',
            fontWeight: 600,
            marginBottom: '0.75rem',
            color: '#e5e7eb'
          }}>
            Country (Optional)
          </label>
          <select
            value={country}
            onChange={(e) => setCountry(e.target.value)}
            style={{
              width: '100%',
              padding: '1rem 1.25rem',
              borderRadius: '0.75rem',
              fontSize: '1rem',
              fontWeight: 500,
              color: '#ffffff',
              backgroundColor: 'rgba(255, 255, 255, 0.1)',
              border: '2px solid rgba(156, 163, 175, 0.3)',
              outline: 'none',
              cursor: 'pointer',
              boxSizing: 'border-box'
            }}
          >
            <option value="" style={{ backgroundColor: '#1a1a2e' }}>Select country</option>
            {COUNTRIES.map((c) => (
              <option key={c.code} value={c.code} style={{ backgroundColor: '#1a1a2e' }}>
                {c.flag} {c.name}
              </option>
            ))}
          </select>

          <div style={{
            display: 'flex',
            alignItems: 'flex-start',
            gap: '0.5rem',
            marginTop: '0.75rem',
            padding: '0.75rem',
            borderRadius: '0.5rem',
            background: 'rgba(59, 130, 246, 0.1)',
            border: '1px solid rgba(59, 130, 246, 0.3)'
          }}>
            <Globe style={{
              width: '1rem',
              height: '1rem',
              color: '#60a5fa',
              flexShrink: 0,
              marginTop: '0.125rem'
            }} />
            <div style={{ flex: 1 }}>
              <p style={{
                fontSize: '0.7rem',
                color: '#93c5fd',
                lineHeight: '1.4'
              }}>
                Private, never shown publicly. Helps improve platform experience.
              </p>
            </div>
          </div>
        </div>

        {error && (
          <div style={{
            ...containerStyle,
            marginBottom: '1.5rem',
            padding: '1rem',
            backgroundColor: 'rgba(239, 68, 68, 0.1)',
            border: '1px solid rgba(239, 68, 68, 0.3)',
            borderRadius: '0.75rem'
          }}>
            <p style={{
              fontSize: '0.875rem',
              color: '#f87171',
              textAlign: 'center'
            }}>
              {error}
            </p>
          </div>
        )}

        <button
          onClick={handleRegister}
          disabled={!canProceed || loading}
          style={{
            ...containerStyle,
            padding: '1.25rem',
            fontSize: '1.125rem',
            fontWeight: 700,
            color: '#ffffff',
            background: canProceed && !loading
              ? 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
              : '#4b5563',
            border: 'none',
            borderRadius: '0.75rem',
            cursor: canProceed && !loading ? 'pointer' : 'not-allowed',
            transition: 'all 0.3s',
            boxShadow: canProceed && !loading
              ? '0 10px 25px -5px rgba(139, 92, 246, 0.5)'
              : 'none',
            marginBottom: '1.5rem'
          }}
        >
          {loading ? '‚è≥ Creating account...' : 'üöÄ Start Matrix TON'}
        </button>

        <div style={{
          ...containerStyle,
          textAlign: 'center',
          padding: '1rem',
          backgroundColor: 'rgba(59, 130, 246, 0.1)',
          border: '1px solid rgba(59, 130, 246, 0.2)',
          borderRadius: '0.75rem'
        }}>
          <p style={{
            fontSize: '0.8rem',
            color: '#93c5fd',
            lineHeight: '1.6'
          }}>
            ‚ÑπÔ∏è Nickname visible to all participants
          </p>
        </div>
      </div>

      <style>{`
        @keyframes pulse {
          0%, 100% { opacity: 0.5; transform: scale(1); }
          50% { opacity: 0.8; transform: scale(1.05); }
        }
      `}</style>
    </div>
  );
}
